@import '../../commonStyles/commonStyles.scss';

.promo {
  width: 100%;
  position: relative;
  @extend %paddings__block;
}

.promo__text {
  @extend %text__regular;
}

.promo__figure {
  background-color: $promoFigure;
  position: absolute;
  width: 868px;
  height: 768px;
  border-radius: 99px;
  transform: rotate(-45deg);
  //right: -10%;
  //top: -70%;
  opacity: 0.5;
  top: -65%;
  right: -10%;
}

.promo__dots {
  background-color: transparent;
  background-repeat: no-repeat;
  background-size: contain;
  background-image: url(../../images/dots.svg);
  opacity: 1;
  position: absolute;
  width: 209px;
  height: 285px;
  right: 0px;
  top: 24px;
}

.promo__title-box {
  position: relative;
  z-index: 2;
  text-align: center;
}

.promo__title {
  @extend %title__main;
  letter-spacing: -3px;
  margin: 0 0 16px 0;
}

.promo__subtitle {
  @extend %title__block_large;
  letter-spacing: -1px;
  margin: 0;
}

.promo__strong-word {
  //color: $promoStrongWord;
  color: #d63031;
  text-shadow: 0px 4px 0px #ffffff;
  position: relative;
  z-index: 2;
  &::after {
    content: '';
    position: absolute;
    width: 191px;
    height: 51px;
    right: -11px;
    top: 60%;
    background-color: #55efc4;
    z-index: -1;
    border-radius: 99px;
    opacity: 1;
  }
}
